import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/som',
    name:'selectOnMap',
    component: () => import('@/views/selectOnMap.vue'),
  },
  {
    path: '/reservoir',
    name:'selectOnMap',
    component: () => import('@/views/reservoirInformation.vue'),
  },
  {
    path: '/upload/ml',
    name:'uploadMeasuredLevel',
    component: () => import('@/views/uploadMeasuredLevel.vue'),
  },
  {
    path: '/upload/rl',
    name:'uploadRadarLevel',
    component: () => import('@/views/uploadRadarLevel.vue'),
  },
  {
    path: '/upload/sar',
    name:'uploadSarImage',
    component: () => import('@/views/uploadSarImage.vue'),
  },
  {
    path: '/upload/opt',
    name:'uploadOpticalImage',
    component: () => import('@/views/uploadOpticalImage.vue'),
  },
  {
    path: '/dm',
    name:'dataManagement',
    component: () => import('@/views/dataManagement.vue'),
  },
  {
    path: '/add-reservoir',
    name:'addReservoir',
    component: () => import('@/views/addReservoir.vue'),
  },
  {
    path: '/model/level',
    name:'EditLevelModel',
    component: () => import('@/views/EditLevelModel.vue'),
  },
]

const router = new VueRouter({
  routes,
  mode: 'history'
})

export default router
