<template>
    <el-menu class="nav" :style="setFlexBasic" :collapse="isCollapse" default-active="1" router>
        <div class="nav-reservoirInfo">
            <el-select class="nav-reservoirInfo-select"  @change="handleSelectChange" v-model="selectedReservoir" v-show="!isCollapse" clearable
                       placeholder="未选择水库">
                <el-option-group label="最近使用" v-if="navRecentReservoirList.length>0">
                    <el-option
                            v-for="item in navRecentReservoirList"
                            :key="item.id"
                            :label="item.name"
                            :value="item.name">
                    </el-option>
                </el-option-group>
                <el-option-group>
                    <el-option label="在地图上选择" value="selectOnMap">
                        <i class="el-icon-place el-icon--left"></i>在地图上选择
                    </el-option>
                </el-option-group>
            </el-select>

            <el-popover
                    placement="right"
                    trigger="click"
                    v-show="isCollapse"
                    class="nav-reservoirInfo-popover">
                <el-select v-model="selectedReservoir" @change="handleSelectChange" clearable placeholder="未选择水库">
                    <el-option-group label="最近使用" v-if="navRecentReservoirList.length>0">
                        <el-option
                                v-for="item in navRecentReservoirList"
                                :key="item.id"
                                :label="item.name"
                                :value="item.name">
                        </el-option>
                    </el-option-group>
                    <el-option-group>
                        <el-option label="在地图上选择" value="selectOnMap">
                            <i class="el-icon-place el-icon--left"></i>在地图上选择
                        </el-option>
                    </el-option-group>
                </el-select>
                <el-button slot="reference" class="nav-reservoirInfo-button-collapse">
                    <i class="el-icon-arrow-right"></i>
                </el-button>
            </el-popover>
        </div>


        <el-menu-item index="/">
            <i class="el-icon-s-home"></i>
            <span slot="title">主页</span>
        </el-menu-item>
        <el-menu-item index="/reservoir" :disabled="uploadDisabled">
            <i class="el-icon-data-line"></i>
            <span slot="title">水文信息</span>
        </el-menu-item>
        <el-submenu index="/upload">
            <template slot="title">
                <i class="el-icon-upload2"></i>
                <span>上传数据</span>
            </template>
            <el-menu-item-group>
                <span slot="title">水位高度</span>
                <el-menu-item index="/upload/ml">实测水位</el-menu-item>
                <el-menu-item index="/upload/rl">雷达高度计</el-menu-item>
            </el-menu-item-group>
            <el-menu-item-group>
                <span slot="title">水域面积</span>
                <el-menu-item index="/upload/sar">SAR影像</el-menu-item>
                <el-menu-item index="/upload/opt">光学影像</el-menu-item>
            </el-menu-item-group>
        </el-submenu>
        <el-menu-item index="/dm">
            <i class="el-icon-files"></i>
            <span slot="title">数据记录管理</span>
        </el-menu-item>
        <el-menu-item index="/setting">
            <i class="el-icon-setting"></i>
            <span slot="title">系统设置</span>
        </el-menu-item>
        <div class="nav-button-div">
            <el-button class="collapse-button" @click="changeCollapse">
                <i class="el-icon-arrow-left" :class="isCollapse?'rotate':'un-rotate'"></i>
            </el-button>
        </div>
    </el-menu>

</template>

<script>
    export default {
        name: "AppNavMenu",
        data: function () {
            return {
                isCollapse: false,
                navRecentReservoirList: [
                    {
                        name: "丹江口水库",
                        id: 1,
                    }, {
                        name: "水库2",
                        id: 2,
                    }, {
                        name: "水库3",
                        id: 3,
                    },
                ],
                selectedReservoir: "",
            }

        },
        methods: {
            changeCollapse: function () {
                this.isCollapse = !this.isCollapse;
            },
            handleSelectChange:function (value) {
                if (value ==='selectOnMap'){
                    this.selectedReservoir = "选择水库"
                    this.$router.push('/som');
                }
                else{
                    this.$router.push('/')
                }
            }
        },
        computed: {
            uploadDisabled: function () {
                return ['', 'selectOnMap'].includes(this.selectedReservoir);
            },
            setFlexBasic: function () {
                if (this.isCollapse) {
                    return {
                        'flex-basis': '65px',
                    }
                } else {
                    return {
                        'flex-basis': '200px',
                    }
                }
            }
        },
        mounted(){
        }
    }
</script>

<style scoped>
    .nav {
        min-height: 100%;
        min-height: 100%;
        display: flex;
        flex-direction: column;
        transition: flex-basis 0.3s;
    }

    .nav::-webkit-scrollbar{
        display: none;
    }

    .nav-reservoirInfo {
        width: 100%;
        margin-top: 10px;
        margin-bottom: 40px;
        display: flex;
        justify-content: center;
    }


    .nav-reservoirInfo-select {
        width: 150px;
        height: 40px;
    }

    .nav-reservoirInfo-popover {
        width: 100%;
    }

    .nav-reservoirInfo-button-collapse {
        width: 100%;
        border: none;
        height: 40px;
        background: #f6f6f6;
    }


    .nav-button-div {
        flex: 1;
        min-height: 80px;
        display: flex;
        flex-direction: column-reverse;
    }

    .collapse-button {
        border: none;
        width: 100%;
        height: 50px;
        margin-bottom: 10px;
        background: none;

    }


    .un-rotate {
        animation: icon-rotate-0 0.2s;
    }

    .rotate {
        animation: icon-rotate-1 0.2s;
        transform: rotate(180deg);
    }

    @keyframes icon-rotate-0 {
        from {
            transform: rotate(180deg);
        }
        to {
            transform: rotate(0deg);
        }
    }

    @keyframes icon-rotate-1 {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(180deg);
        }
    }


</style>
